import React from 'react'
import '../../../App.css'
import { ThemeProvider } from "@material-ui/core/styles";
import { ParallaxBanner } from 'react-scroll-parallax';
import anomaly0 from './anomaly0.png'
import anomaly1 from './anomaly1.png'
import anomaly2 from './anomaly2.png'
import anomaly3 from './anomaly3.png'
import anomaly4 from './anomaly4.png'
import { useController } from 'react-scroll-parallax';
console.log(anomaly0)

const images = [anomaly0,anomaly1,anomaly2,anomaly3,anomaly4]

const paralax = images.map((_,i)=>1-((i-images.length)**2)/images.length**2 ).reverse()
let layers = images.map((_,i)=>({
    image: images[i],
    amount: paralax[i],
    alt:"blackgirl",
    expanded:false
  }))

//.52
  const speed = [
    0.555,
    0.75,
    0.7,
    0.60,
    .80
  ]

  layers = layers.map((l,i)=>({
    ...l,
    amount:speed[i]
  }))

  console.log(layers)


const Anomaly = () => {
  const { parallaxController } = useController();
  window.onresize = () => {
    parallaxController.update()
  } 
  console.log(layers.map(e=>e.amount))

  return (
    <ThemeProvider >
      <div className="Black-girl">
        <div 
          style={{
            height:'300vh',
            backgroundColor: '#000a13', 
            width:"100%"
          }}
        />
        <ParallaxBanner
          layers={layers}
          style={{
            position:'absolute',
            backgroundColor: '#000a13', 
            width:'65vh',
            maxWidth:'100vw',
            height: '125vh',
            top: '50vh'
          }}
        />
        </div>  
    </ThemeProvider>
  )
}


export default Anomaly